<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }

</style>

<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-form-item label="轮播标题">
                <el-input style="width: 500px" v-model="form.title" placeholder="请输入"></el-input>

            </el-form-item>

            <el-form-item label="外链地址">
                <el-input style="width: 500px" v-model="form.url" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="轮播图">
                <image-select v-model="img" :url="form.picture"></image-select>
            </el-form-item>

            <el-form-item label="排序">
                <el-input style="width: 200px" v-model="form.sort" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="状态">
                <el-select v-model="form.status" placeholder="请选择">
                    <el-option
                            label="启用"
                            :value="1">
                    </el-option>
                    <el-option
                            label="不启用"
                            :value="0">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item style="margin-top: 100px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmit">提交
                </el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

    import api from '../../js/api/carousel'

    export default {
        data() {
            return {
                imageUrl: '',
                form: {
                    id: null,
                    url: null,
                    title: null,
                    picture: null,
                    sort: 1,
                    status: null,
                },
                img: null,
                isNoEdit: true,
                isLoading: false,
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                if (this.$route.query.id !== undefined) {
                    this.isNoEdit = true;
                    this.loadData()
                } else {
                    this.isNoEdit = false;
                }
            },
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                })
            },
            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            edit() {
                this.isNoEdit = false;
            },
            onSubmit() {
                if (!api.isStrValid(this.form.title)) {
                    this.$message.error('请输入名称');
                    return;
                }
                if (this.img == null && this.form.picture == null) {
                    this.$message.error('请选择图片');
                    return;
                }
                let data = api.objToFormData(this.form);
                data.append('picture', this.img);
                this.loading = true;
                api.upload(data, () => {
                    this.loading = false;
                    this.$message.success('保存成功!');
                    this.$router.push({name: 'carousel'});
                    this.$route.meta.keepAlive = false;

                }, () => {
                    this.loading = false;
                })
            },
            back() {
                this.$router.back();
            },

        }
    }
</script>
